import { CustomerIoEventProperties } from "./adapters.type"

const stringifyData = (
  data: CustomerIoEventProperties,
): Record<
  string,
  string | boolean | null | Array<string> | Array<Record<string, string | boolean>>
> => {
  return Object.fromEntries(
    Object.entries(data).map(([key, value]) => {
      if (typeof value === "number") {
        return [key, String(value)]
      }
      return [key, value]
    }),
  )
}

export { stringifyData }
